@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,500;0,700;1,300&display=swap);
.interface {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100vh;
  width: 100%;
}

.header * {
  background-color: #202020;
}

.header {
  word-spacing: 1.5px;
  background-color: #202020;
  width: 100%;
  padding: 20px;
  text-align: center;
  line-height: 30px;
  border-bottom: 1px #bdbdbd solid;
}

.header h1 {
  font-size: 1.8em;
  letter-spacing: 2px;
}

.header span {
  font-weight: 700;
}

.header span.title1 {
  color: #bdbdbd;
}

.header span.title2 {
  color: #FF0000;
}

.main {
  display: flex;
  flex-direction: column;
}

.main input {
  text-align: center;
  font-family: Roboto;
  font-weight: 300;
  color: #f8f8f8;
  padding: 4px;
  border: none;
  border-bottom: 2px #ff0000 solid;
  width: 18em;
  margin: 10px;
  align-self: center;
  outline: none;
}

.main button {
  background-color: #ff0000;
  align-self: center;
  padding: 4px;
  border: none;
  color: #f8f8f2;
  width: 40%;
  height: 35px;
}

.footer * {
  background-color: #202020;
} 

.footer {
  
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;

  padding: 20px;

  border-top: 1px #bdbdbd solid;
  width: 100%;
  background-color: #202020;
}

.footer a {
  text-decoration: none;
} 

.footer .name1 {
  color: #ff0000;
  font-weight: 700;
}

.footer .name2 {
  color: #bdbdbd;
  font-weight: 700;
}


.modal {
  display:  flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  height: 100vh;
}

.video-player * {
  background-color: rgba(0, 0, 0, 0.2);
}

.video-player {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  width: 400px;
  margin: 10px;
  padding: 10px;
  background-color:rgba(0, 0, 0, 0.6);
  border: #d84141 4px ridge;
  word-wrap: break-word;
}

.video-player h1 {
  align-self: center;
  margin: 8px;
  font-weight: 700;
  font-size: 1.3em;
  color: #d84141; 
  background-color:rgba(0, 0, 0, 0.2);
}

.video-player p {
  font-size: 1.1em;
  text-align: justify;
}

.video-player span {
  font-weight: 500;
  font-size: 1.1em;
  color: #d84141;
}

.video-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 20px;
  background-image: linear-gradient(#3e0000, #282a36, #3e0000, #282a36, #3e0000);
}

.video-grid img {
  position: fixed;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  left: 5%;
  border: solid 2px #d84141;
}

* {
  padding:  0;
  margin: 0;
  font-family: Roboto;
  font-weight: 400;
  font-size: 100%;
  box-sizing: border-box;
  color: #f8f8f2;
  background-color: #282828;
}

